import React from 'react';
import HairImg from './imgs/healthyhair.png';

const HealthyHair = () => {
    return (
		<div>
			<div className='pa5'>
				<div className='f1 tc pa3 white'>Healthy Hair</div>
				<div className='flex'>
					<div className='f3 pa5'>
					There are many types of hair. Each of those types can benefit from specific products. How do you want your hair? Long hair with volume, short-sleek, or maybe you like a simple pony. Need a buzz cut, flat top, or maybe long tied back. Every cut and style can benefit from a shampoo and conditioner that will bring out the best in your hair! Talk to me today and I'll happy to help you with all your hair health needs.
					</div>
					<img className='pa3' alt='Healthy Hair' src={HairImg} />
				</div>
			</div>
	    </div>
    )
}

export default HealthyHair;