import React, { Component } from 'react';
import Home from './Components/Home';
import HealthyHair from './Components/HealthyHair';
import Cuts from './Components/Cuts';
import Book from './Components/Book';
import icon from './Components/imgs/ASicon.png';
import './App.css';

class App extends Component {
  constructor() {
    super(...arguments);
    this.state = { 
      route: 'cuts'
    };
  }


  render() {
    const click = (event) => {
      this.setState({'route': event.target.id })
    }
    return (
      <html className='bgblue'>
        <body className='mw9'>
          <div className=''>
            <nav className="bgblue">
              <ul className="nav items-center">
                  <li className="">
                    <img className='' alt='test' src={icon} />                 
                  </li>
                  <li onClick={click} className=' pointer' id='home'>Home</li>
                  <li className='dropdown'>
                    <span onClick={click} id='cuts' className='pointer'>Hair Styles</span>
                    <ul className='dropdown-content'>
                      <li onClick={click} className=' pointer' id='ladies'>Ladies</li>
                      <li onClick={click} className=' pointer' id='men'>Men</li>
                      <li onClick={click} className=' pointer' id='girls'>Girls</li>
                      <li onClick={click} className=' pointer' id='boys'>Boys</li>
                    </ul>
                  </li>
                  <li onClick={click} className='pointer' id='book'>Book Appointment</li>
                  <li onClick={click} className='pointer' id='healthyhair'>Be Healthy</li>                
                  <li className="push">
                    <span className=''>(619) 333 - 3333</span>
                  </li>
              </ul>
            </nav>
            <div className="main bgpurple pa3">
            { this.state.route === 'cuts' 
              ? <Cuts />
              : null
            }
            { this.state.route === 'healthyhair' 
              ? <HealthyHair />
              : null
            }
            { this.state.route === 'home' 
              ? <Home />
              : null
            }
            { this.state.route === 'book' 
              ? <Book />
              : null
            }
            </div>
          </div>
        </body>
      </html>
    );
  }
}

export default App;
